.wrapper {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 2fr 2fr;
}

.card {
    display: grid;
    grid-template-columns: 2fr 8fr;
    background: #212121;
    align-items: center;
    padding-left: 10px;
    border-radius: 15px;
}

.card img {
    height: 60px;
    width: 60px;
    background:#eee;
    border-radius: 100%;
    margin-left: 15px;

}

.card h4 {
    padding-top: 10px;
    color: #ffd800;
}