.wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
}

.card {
    background: #212121;
    padding: 10px;
    border-radius: 10px;
}

.card h4 {
    color: yellow;
}

.card p {
    font-style: italic;
}

.card a {
    display: flex;
    justify-content: end;
    text-decoration: none;
    color: #2183c6;
}