.navbar {
    width: 200px;
    background: #0b0b0b;
    height: 60%;
    grid-area: n;
    margin-left: 20px;
    position: fixed;
    margin-top: 70px;
    border-radius: 15px;
    /* box-shadow: 0px 0px 10px 0px #3a3acf; */
    box-shadow: 0 0 10px 0 #3a3a3a;
}

.menu {
    color: aliceblue;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
}

.menuItem {
    padding: 5px;
}

.item {
    text-decoration: none;
    color: white;
    font-size: 20px;
}
