.wrapper {
    width: 350px;
    margin: 12% auto;
    background: #212121;
    font-size: 1.3em;
    padding: 10px;
}

.register h3 {
    color: yellow;
    float: left;
}

.register input {
    background: #000;
    width: 90%;
    height: 40px;
    border-radius: 3px;
    margin: 5px 0;
    outline: none;
    border-radius: 8px;
    color: yellow;
}

.register button {
    background: yellow;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1em;
    height: 42px;
    width: 90%;
    margin-top: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    transition: all .3s cubic-bezier(.6, 0, .4, 1);
}

.register button:hover {
    background: #ffd800;
}