.App {
    display: grid;
    grid-template-areas: 'h h''n c ';
    grid-gap: 10px;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    background: #212121;
}

.content {
    grid-area: c;
    background: #0b0b0b;
    margin-right: 20px;
    color: white;
    padding: 20px;
    border-radius: 15px;
    min-height: 800px;
    /* box-shadow: 0px 0px 10px 0px #3a3acf; */
    box-shadow: 0 0 10px 0 #3a3a3a;
    margin-bottom: 20px;
}