.header {
    grid-area: h;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
    background: #000;
    color: #fff;
    position: fixed;
    /* box-shadow: 0px 0px 10px 0px #3a3acf; */
    box-shadow: 0 0 10px 0 #3a3a3a;
}



.logo {
    padding-left: 40px;
}

.auth a{
    margin-right: 40px;
    background: #ffd800;
    padding: 6px;
    color: #000;
    border-radius: 30px;
    cursor: pointer;
    text-decoration: none;
}
